.card {
    text-align: center;
    -webkit-box-shadow: 0 3px 16px 0 rgba(29, 42, 233, 0.2),0 6px 20px 0 rgba(30, 98, 245, 0.19) !important;
            box-shadow: 0 3px 16px 0 rgba(29, 42, 233, 0.2),0 6px 20px 0 rgba(30, 98, 245, 0.19) !important;

}

h1 {
    font-size: 60px;
}

.rounded {
    clip: rect(0px, 200px, 200px, 0px)
}

.text-uppercase {
    font-size: 5vw;
    margin-top: 5vh;
    font-weight: bold;
}

.container .text-muted {
    margin: 20px 0;
}

.list-group-item {
    border-radius: 0 !important;
}

.list-group-item:last-child {
    border-width: 1px 0 !important;
}

/* Required styles */

.swipe-to-delete {
    border-radius: 20px;
    margin-bottom: 5px;
    position: relative !important;
    padding: 0 !important;
    overflow: hidden !important;
}

.swipe-to-delete .js-delete {
    position: absolute !important;
    top: 0 !important;
    right: 0 !important;
    bottom: 0 !important;
    left: 0 !important;
    z-index: 1 !important;
}

.swipe-to-delete .js-content {
    position: relative !important;
    z-index: 2 !important;
    -webkit-user-select: none !important;
    -moz-user-select: none !important;
    -ms-user-select: none !important;
}

.swipe-to-delete .js-content :first-child {
    position: relative !important;
}

.swipe-to-delete .js-transition-delete-right,
.swipe-to-delete .js-transition-delete-left,
.swipe-to-delete .js-transition-cancel {
    -webkit-transition-property: left !important;
    -o-transition-property: left !important;
    transition-property: left !important;
    -webkit-transition-duration: .5s;
         -o-transition-duration: .5s;
            transition-duration: .5s;
}

.swipe-to-delete .js-transition-delete-right {
    left: 100% !important;
}

.swipe-to-delete .js-transition-delete-left {
    left: -100% !important;
}

.swipe-to-delete .js-transition-cancel {
    left: 0 !important;
}

/* Custom styles */

.swipe-to-delete .js-delete {
    background: #ff5f58;
}

.swipe-to-delete .js-delete svg {
    position: absolute;
    left: 17px;
    top: 50%;
    margin-top: -13px;
    width: 25px;
    height: 25px;
}

.swipe-to-delete .js-delete svg:first-child {
    left: auto;
    right: 17px;
}

.swipe-to-delete .js-content :first-child {
    cursor: pointer;
}

.swipe-to-delete .js-transition-delete-right,
.swipe-to-delete .js-transition-delete-left,
.swipe-to-delete .js-transition-cancel {
    -webkit-transition-duration: .7s;
         -o-transition-duration: .7s;
            transition-duration: .7s;
    -webkit-transition-timing-function: ease-in-out;
         -o-transition-timing-function: ease-in-out;
            transition-timing-function: ease-in-out;
}

#itemForm{
    margin-top: 3%;
}

input{
    font-size: 3vh;
}


